import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';



@Injectable({
  providedIn: 'root'
  
})
export class InfoService {

  constructor(
   private info:HttpClient
  ) { }

  getInfo(){
    return this.info.get('https://jsonplaceholder.typicode.com/comments');
  }
    
}
