import { Component, OnInit, Input } from '@angular/core';
import { ProjectsService} from '../../services/projects/projects.service';
import { from } from 'rxjs';
import {  AngularFirestore } from '@angular/fire/firestore';
import {  map } from 'rxjs/operators';

@Component({
  selector: 'app-php',
  templateUrl: './php.component.html',
  styleUrls: ['./php.component.css']
})
export class PhpComponent implements OnInit {
  @Input()  proj:any[]=[];
  constructor(
    // private project:ProjectsService,
    private fire:AngularFirestore

  ) { }

  ngOnInit() {
    // this.project.getProjects(2).subscribe(data=>{this.proj=data});
    this.fire.collection('projects').get().pipe(map(data=>{
      data.forEach(element => {
        if(element.data().id==2){
          this.proj.push({
            name:element.data().name,
            url:element.data().url,
            description:element.data().description,
            img:element.data().img,

          })
        }
      });
    })).subscribe(a=>{
      // console.log(a);
    })
  }

}
