import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule,Routes } from '@angular/router';
import{AngularFireAuthModule } from '@angular/fire/auth';

import { InfoComponent } from './components/info/info.component';
import { CustomersComponent } from './components/customers/customers.component';
import { Route } from '@angular/compiler/src/core';
import{ContactsComponent} from './components/contacts/contacts.component';
import{ProjectsComponent} from './components/projects/projects.component';
import{JavaScriptComponent} from './components/java-script/java-script.component';
import{ PhpComponent } from './components/php/php.component';
import{ LaravelComponent } from './components/laravel/laravel.component';
import{ ApiComponent } from './components/api/api.component';
import{AboutComponent} from './components/about/about.component';
import { from } from 'rxjs';
import{ CustomerAddComponent } from './components/customer-add/customer-add.component';
import { SignInComponent} from './components/sign-in/sign-in.component';
import  {BlogComponent } from './components/blog/blog.component';
import{PageNotFoundComponent} from './components/page-not-found/page-not-found.component';





const appRoutes: Routes=[
  {path:'',redirectTo:'about',pathMatch:'full'},
  {path: 'signin',component:SignInComponent},
  {path:'customers',component:CustomersComponent},
  {path:'contacts',component:ContactsComponent},
  {path: 'projects',component:ProjectsComponent},
  {path: 'javaScript',component:JavaScriptComponent},
  {path: 'php',component:PhpComponent},
  {path: 'laravel',component:LaravelComponent},
  {path: 'api',component:ApiComponent},
  {path: 'about',component:AboutComponent},
  {path: 'info',component:InfoComponent},
  {path: 'customer/add',component:CustomerAddComponent},
  {path: 'blog',component:BlogComponent},
  {path: '**',component:PageNotFoundComponent}
]


@NgModule({
  exports:[RouterModule],
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes),
    AngularFireAuthModule
  ]
})
export class AppRoutingModule { }
